.formInput {
  /* display: block; */
  border-width: 0rem;
  border-bottom-width: 0.1rem;
  border-bottom-color: rgb(112, 112, 255);
  min-width: 100%;
  font-size: medium;
  padding: 0.8rem;
  margin-bottom: 1rem;
  /* background-color: grey; */
}

.formInput#panNo {
  text-transform: uppercase;
}

.formInput:focus-visible {
  outline: none;
  border-width: 0rem;
  border-bottom-width: 0.15rem;
  border-bottom-color: rgb(116, 197, 116);
  transition: border-bottom-color 300ms ease-out 50ms;
}
.formInput:disabled {
  background-color: rgb(236, 237, 234);
  color: black;
  font-weight: 600;
}
label {
  color: black;
  text-align: start;
  min-width: 100%;
  font-weight: 500;
}

/* Button Related CSS */
.formBtnComponenet {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
}

.formNextButtonContainer {
  display: flex;
  padding: 0.5rem;
  justify-content: end;
}

.formNextButton {
  background-color: rgb(112, 112, 255);
  padding: 0.3rem;
  min-width: 5rem;
  border-radius: 0.6rem;
  border-style: none;
  /* border-color: none; */
  /* outline: none; */
}
.formNextButton:hover {
  background-color: rgb(207, 158, 67);
}
.formNextButton:focus {
  background-color: orange;
  color: white;
  /* transition: background-color 2s; */
}
/* .formHeading {
  text-align: start;
  color: rgb(255, 77, 0);
  margin-bottom: 0.5rem;
} */

.formCheckBoxContainer {
  display: flex;
  color: #2b5575;
}

.formCheckbox {
  background-color: red;
  height: 1.4rem;
  width: 1.4rem;
  color: red;
  margin-right: 0.5rem;
  align-items: start;
}
label[for="basicInfoTandC"] {
  display: inline;
  font-size: 0.9rem;
}

/* paragraph */
.formPara {
  font-size: large;
  text-align: left;
}

#inputStyle {
  width: 3rem !important;
}

@media screen and (max-width: 640px) {
  .formInput {
    /* border-width: 0.1rem; */
    /* border-style: double; */
    /* border-color: black; */
    border-bottom-width: 0.1rem;
    border-bottom-color: rgb(112, 112, 255);
    min-width: 100%;
    font-size: medium;
    padding: 0.8rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
    /* border-radius: 0.5rem; */
    background-color: white;
  }

  .formInput:focus-visible {
    outline: none;
    border-width: 0rem;
    border-bottom-width: 0.15rem;
    border-bottom-color: rgb(116, 197, 116);
    transition: border-bottom-color 300ms ease-out 50ms;
  }
  .formInput:disabled {
    background-color: rgb(238, 233, 233);
    /* border-radius: 1rem; */
    color: rgba(29, 54, 29, 0.949);
  }
}

